import React from "react";
import PropTypes from "prop-types";
import { graphql, Link } from "gatsby";

import { Layout, PostCard, Pagination } from "../components/common";
import { MetaData } from "../components/common/meta";

import "../components/common/Layout/post.scss";

/**
 * Main index page (home page)
 *
 * Loads all posts from Ghost and uses pagination to navigate through them.
 * The number of posts that should appear per page can be setup
 * in /utils/siteConfig.js under `postsPerPage`.
 *
 */
const Index = ({ data, location, pageContext }) => {
    const posts = data.allGhostPost.edges;

    posts.forEach((post) => {
        post.node.featured = false;
    });

    posts[0].node.featured = true;

    const techPosts = data.techPosts.edges;
    techPosts.forEach((post) => {
        post.node.featured = false;
    });

    techPosts[0].node.featured = true;

    const dataPosts = data.dataPosts.edges;
    dataPosts.forEach((post) => {
        post.node.featured = false;
    });

    dataPosts[0].node.featured = true;

    const designPosts = data.designPosts.edges;
    designPosts.forEach((post) => {
        post.node.featured = false;
    });

    designPosts[0].node.featured = true;

    const storiesPosts = data.storiesPosts.edges;
    storiesPosts.forEach((post) => {
        post.node.featured = false;
    });

    storiesPosts[0].node.featured = true;

    const culturePosts = data.culturePosts.edges;
    culturePosts.forEach((post) => {
        post.node.featured = false;
    });

    culturePosts[0].node.featured = true;

    const tags = data.tags.edges;
    return (
        <div className="first-section">
            <MetaData location={location} />
            <Layout location="/blog/">
                <div className="p-0">
                    <div className="bg-dark kernel-banner go-jek-work-gradient relative-position" />
                </div>

                <div className="container my-5">
                    {tags.map(({ node }) => (
                        <Link
                            key={node.slug}
                            to={`tag/${node.slug}`}
                            className="text-green mr-2"
                        >
                            <span className="badge tag">{node.name}</span>
                        </Link>
                    ))}
                    <Link
                        key="all"
                        to="articles/all"
                        className="text-green mr-2"
                    >
                        <span className="badge tag">All</span>
                    </Link>
                </div>

                <div className="container">
                    <h2 className="maison-bold">Latest</h2>
                    <section className="post-feed">
                        <div className="row">
                            {posts.map(({ node }) => (
                                <PostCard key={node.id} post={node} />
                            ))}
                        </div>
                        <div className="my-4">
                            <Link to="/articles/all" className="text-green">
                                View All{` `}
                                <i className=" fa fa-arrow-right" />
                            </Link>
                        </div>
                    </section>

                    {/* Tech Started Posts */}
                    <h2 className="maison-bold">Tech</h2>
                    <section className="post-feed">
                        <div className="row">
                            {techPosts.map(({ node }) => (
                                <PostCard key={node.id} post={node} />
                            ))}
                        </div>
                        <div className="my-4">
                            <Link to="/tag/tech" className="text-green">
                                View All{` `}
                                <i className=" fa fa-arrow-right" />
                            </Link>
                        </div>
                    </section>
                    {/* End Tech posts */}

                    {/* Data Started Posts */}
                    <h2 className="maison-bold">Data</h2>
                    <section className="post-feed">
                        <div className="row">
                            {dataPosts.map(({ node }) => (
                                <PostCard key={node.id} post={node} />
                            ))}
                        </div>
                        <div className="my-4">
                            <Link to="/tag/data" className="text-green">
                                View All{` `}
                                <i className=" fa fa-arrow-right" />
                            </Link>
                        </div>
                    </section>
                    {/* End Data posts */}

                    {/* Design Started Posts */}
                    <h2 className="maison-bold">Design</h2>
                    <section className="post-feed">
                        <div className="row">
                            {designPosts.map(({ node }) => (
                                <PostCard key={node.id} post={node} />
                            ))}
                        </div>
                        <div className="my-4">
                            <Link to="/tag/design" className="text-green">
                                View All{` `}
                                <i className=" fa fa-arrow-right" />
                            </Link>
                        </div>
                    </section>
                    {/* End Design posts */}

                    {/* Stories Started Posts */}
                    <h2 className="maison-bold">Stories</h2>
                    <section className="post-feed">
                        <div className="row">
                            {storiesPosts.map(({ node }) => (
                                <PostCard key={node.id} post={node} />
                            ))}
                        </div>
                        <div className="my-4">
                            <Link to="/tag/stories" className="text-green">
                                View All{` `}
                                <i className=" fa fa-arrow-right" />
                            </Link>
                        </div>
                    </section>
                    {/* End Stories posts */}

                    {/* Culture Started Posts */}
                    <h2 className="maison-bold">Culture</h2>
                    <section className="post-feed">
                        <div className="row">
                            {culturePosts.map(({ node }) => (
                                <PostCard key={node.id} post={node} />
                            ))}
                        </div>
                        <div className="my-4">
                            <Link to="/tag/culture" className="text-green">
                                View All{` `}
                                <i className=" fa fa-arrow-right" />
                            </Link>
                        </div>
                    </section>
                    {/* End Culture posts */}
                </div>
            </Layout>
        </div>
    );
};

Index.propTypes = {
    data: PropTypes.shape({
        allGhostPost: PropTypes.object.isRequired,
    }).isRequired,
    location: PropTypes.shape({
        pathname: PropTypes.string.isRequired,
    }).isRequired,
    pageContext: PropTypes.object,
};

export default Index;

// This page query loads all posts sorted descending by published date
// The `limit` and `skip` values are used for pagination
export const pageQuery = graphql`
    query GhostPostQuery($skip: Int!) {
        latestFeaturedPost: allGhostPost(
            limit: 1
            filter: { featured: { eq: true } }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        allGhostPost: allGhostPost(
            limit: 4
            filter: { featured: { eq: false } }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        techFeaturedPost: allGhostPost(
            limit: 1
            filter: {
                featured: { eq: true }
                tags: { elemMatch: { slug: { eq: "tech" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        techPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "tech" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        dataFeaturedPost: allGhostPost(
            limit: 1
            filter: {
                featured: { eq: true }
                tags: { elemMatch: { slug: { eq: "data" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        dataPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "data" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        designFeaturedPost: allGhostPost(
            limit: 1
            filter: {
                featured: { eq: true }
                tags: { elemMatch: { slug: { eq: "design" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        designPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "design" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        storiesFeaturedPost: allGhostPost(
            limit: 1
            filter: {
                featured: { eq: true }
                tags: { elemMatch: { slug: { eq: "stories" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        storiesPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "stories" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        cultureFeaturedPost: allGhostPost(
            limit: 1
            filter: {
                featured: { eq: true }
                tags: { elemMatch: { slug: { eq: "culture" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        culturePosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "culture" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        tags: allGhostTag {
            edges {
                node {
                    name
                    slug
                }
            }
        }
    }
`;
